
.admin-center-page,
.app-market-page,
.distribution-flow-page,
.iso-community-page,
.document-distribution-page {
  background-color: #f0f2f5;
  padding-bottom: 20px;
}
.admin-center-page .wrapper,
.app-market-page .wrapper,
.distribution-flow-page .wrapper,
.iso-community-page .wrapper,
.document-distribution-page .wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 800px;
  margin: 0 auto;
}
.admin-center-page .wrapper .sidebar,
.app-market-page .wrapper .sidebar,
.distribution-flow-page .wrapper .sidebar,
.iso-community-page .wrapper .sidebar,
.document-distribution-page .wrapper .sidebar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 178.5px;
          flex: 0 0 178.5px;
  margin-right: 20px;
  margin-top: 20px;
}
.admin-center-page .wrapper .right-column,
.app-market-page .wrapper .right-column,
.distribution-flow-page .wrapper .right-column,
.iso-community-page .wrapper .right-column,
.document-distribution-page .wrapper .right-column {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 0px;
          flex: 1 1 0;
  margin-top: 20px;
}


